<template>
  <div id="improve-info">
    <div class="main">
    	<img class="login-logo" src="../../assets/home/logo.png"/>
      <div class="improve-main">
        <h3>完善信息</h3>
        <div class="info-avatar">
          <span v-if="avatar == ''">头像</span>
          <img :src="avatar" v-else class="avatar">
          <input type="file" accept="image/*" id="image_input" @change="uploadImg">
        </div>
        <input type="text" v-model="name" class="bb-ccc" placeholder="请输入昵称">
        <div class="btn" :class="{active:noEmpty}" @click="finish">完成</div>
      </div>
    </div>
  </div>
</template>

<script>
  import md5 from 'blueimp-md5';
	
  export default {
    data () {
      return {
        name: '',
        avatar: '',
        psw:'',
        hasUpLoad:false,
        cos:'',
        accessUrl:'',
        sourceUrl:'',
        oFile:'',
      }
    },
    watch: {},
    computed:{
    	noEmpty(){
    		if(this.name.length > 0){
    			return true
    		}else{
    			return false
    		}
    	},
    },
    created(){
    	this.psw = this.$route.params.psw
    },
    methods: {
      uploadImg(){
      	this.hasUpLoad = true
				var oFile = document.getElementById("image_input").files[0];
				this.oFile = oFile
				
				var filename = oFile.name
        
        var reader = new FileReader();
        
				let params = {
					a: 1,
					type: 1,
					filename: filename,
					oFile: oFile,
				}
				this.getExternalUploadObject(params)
        
				reader.readAsDataURL(oFile);
        reader.onload = (oFREvent) =>{
          // reader.result;
          this.avatar = oFREvent.currentTarget.result
					document.getElementById("image_input").value = '';
//          console.log(reader.result);
        }

      },
      getExternalUploadObject(params) {
				this.$Api.getExternalUploadObject(params).then((res) => {
					console.log(res)
					if(res.q.s == 0) {
						this.cos = res.q.cos
						this.accessUrl = res.q.cos.accessUrl
						this.sourceUrl = res.q.cos.sourceUrl
					}
				})
			},
			getExternalFilesSubmit(){
				let fileInfos = []
				let fileInfo ={
					accessUrl : this.accessUrl,
					sourceUrl : this.sourceUrl
				}
				fileInfos.push(fileInfo)
				this.$Api.getExternalFilesSubmit(1,1,fileInfos).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						let id = res.q.files[0].id
						//上传图片
						this.upLoadToCos()
						//更新头像
						this.SetImage(id)
					}
				})
			},
			SetImage(id){
				this.$Api.SetImage(2,id).then((res)=>{
					console.log(res)
				})
			},
			upLoadToCos(){
				let url = this.cos.url
				let file = this.oFile
				this.$upLoadToCos(url,file)
			},
			finish(){
				if(this.hasUpLoad){
					this.getExternalFilesSubmit()
				}
				this.getUserRegisterSubmit()
			},
			
			//提交注册
			getUserRegisterSubmit(){
				let user = {
	        name: this.name,
	        sex:1,
	        password: md5(this.$paykey + md5(this.psw))
	      }
	      this.$Api.UserRegisterSubmit(user).then((res) => {
	        console.log(res)
	        if(res.q.s == 0) {
						this.$router.replace({name:'Index'})
	        }else {
	          this.$toast(res.q.d)
	        }
	      })
			},
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #improve-info{
    height: 100%;
    background: #fff;
    width: 100%;

    .main{
    	height: 100%;
    	.login-logo{
    		width: 100%;
    		height: 7.2rem;
    	}
      /*height: 100%;
      background: url("../../assets/home/logo.png") no-repeat center top;
      background-size: 100% auto;*/
    }
    .improve-main{
      width: 80%;
      height: 7.36rem;
      padding: 0.44rem;
      /*position: absolute;
      bottom: 2rem;
      left: 1rem;*/
      margin:0 auto;
      box-shadow: 0rem -0.07rem 0.13rem 0.01rem rgba(67, 67, 67, 0.06), 0rem 0.07rem 0.13rem 0rem rgba(0, 0, 0, 0.13);
      border-radius: 0.11rem;
      h3{
        text-align: center;
        font-size: 0.48rem;
        color: #de7fef;
        margin-bottom: 0.25rem;
      }
      input{
        width: 100%;
        height: 1.2rem;
        border: none;
        font-size: 0.37rem;
        color: #333;
        background: #fff;
        position: relative;
      }
      .bb-ccc{
        border-bottom: 1px #d8d8d8 solid;
      }
    }
    .info-avatar{
      width: 2rem;
      height: 2rem;
      background-color: #cccccc;
      border: solid 0.01rem #cccccc;
      margin: 0.53rem auto;
      border-radius: 1rem;
      line-height: 2rem;
      text-align: center;
      color: #fff;
      position: relative;
      overflow: hidden;
      font-size: 0.45rem;

      input#image_input{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        filter:alpha(opacity=0);
        -moz-opacity:0;
        -khtml-opacity: 0;
        opacity: 0;
      }

      .avatar{
        width: 100%;
        height: 100%;
        border-radius: 1rem;
      }
    }
    .btn{
      width: 6.93rem;
      height: 1.07rem;
      background: #ccc;
      border-radius: 0.53rem;
      margin: 0 auto;
      line-height: 1.07rem;
      color: #fff;
      font-size: 0.43rem;
      text-align: center;
      margin-top: 0.53rem;
    }
		.btn.active{
			background-image: linear-gradient(135deg,
      #fe9bba 0%,
      #de7fef 100%);
		}
  }

</style>



